import React from "react";
import Pagetitle from "../elements/Pagetitle";

import Nishanth from "../../images/Nishanth.jpg";
/* 
Just starting out as a Full Stack Developer I find the struggle and research into getting code to work the way I want, 
to be extremely gratifying! The challenge keeps me learning every day. 
Currently I'm looking for new opportunities and would love to have a conversation about what I can contribute to your team.
*/
const aboutContent = {
  name: "Nishanth Bhat",
  avatarImage: Nishanth,
  content: `I'm an asirping software developer.
     I specialize in using javascript libriaries/frameworks like react.js, express.js and next.js. 
     I'm very hardworking who isn't afraid to face new challenges. 
     I'm a senior at Symbiosis Institute of Technology, Pune. Currently look for opportunities in full stack developer. 
     `,
};

function About() {
  return (
    <section id="about">
      <div className="container">
        <Pagetitle title="About Me" />
        <div className="row">
          <div className="col-md-3">
            <div className="text-center text-md-left">
              <img src={aboutContent.avatarImage} alt={aboutContent.name} />
            </div>
            <div className="spacer d-md-none d-lg-none" data-height="30"></div>
          </div>

          <div className="col-md-9 triangle-left-md triangle-top-sm">
            <div className="rounded bg-white shadow-dark padding-30">
              <div className="row">
                <div className="col-md-6">
                  <p>{aboutContent.content}</p>
                  <div className="mt-3">
                    <a
                      target="_blank"
                      rel="noopener noreferrer"
                      href="https://drive.google.com/file/d/1-Bh8wtubrIJrvZzdRofuyRl8K8SL0NFZ/view?usp=sharing"
                      className="btn btn-default"
                    >
                      Download CV
                    </a>
                  </div>
                  <div
                    className="spacer d-md-none d-lg-none"
                    data-height="30"
                  ></div>
                </div>
                <div className="col-md-6">
                  <span style={{ fontWeight: "bold" }}>Skills:</span>
                  <div className="row">
                    <div className="col">
                      <ul>
                        <li>JavaScript</li>
                        <li>React.js</li>
                        <li>Express.js</li>
                        <li>Python</li>
                        <li>React-native</li>
                        <li>Bootstrap 4</li>
                        <li>HTML5</li>
                      </ul>
                    </div>

                    <div className="col">
                      <ul>
                        <li>CSS3</li>
                        <li>SCSS</li>
                        <li>GCP</li>

                        <li>SQL</li>
                        <li>MongoDB</li>
                      </ul>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div className="spacer" data-height="70"></div>
      </div>
    </section>
  );
}

export default About;
