import React from "react";
import Pagetitle from "../elements/Pagetitle";
import Timeline from "../elements/Timeline";

const educationData = [
  {
    id: 1,
    content: "Bachelors of Technology in Computer Science",
    years: "2018 - Present",
    title: "Symbiosis Institute of Technology",
  },
  {
    id: 2,
    content: "12th Grade",
    years: "2016 - 2018",
    title: "The Lexicon International School",
  },
  {
    id: 3,
    content: "10th Grade",
    years: "2004 - 2016",
    title: "The Bishop's CO-ED School",
  },
];

const experienceData = [
  {
    id: 0,
    title: "SDE Intern, Bajaj Finserv Health",
    years: "Jan 2022 - Present",
    content: `
    I am responsible for developing 
    features and fixing bugs on the 
    app of Bajaj Finserv Health. I
    am currently working on App Development 
    using React Native.
      `,
  },
  {
    id: 1,
    title: "Web Development Intern, StackNation",
    years: "Jul 2020 - Sept 2020",
    content: `
      My responsibilities were working on front-end, 
      back-end and integrate them using REST API. 
      MERN stack was used for developing the application.
      `,
  },
];

function Experiences() {
  return (
    <section id="experience">
      <div className="container">
        <Pagetitle title="Experience" />
        <div className="row">
          <div className="col-md-6">
            <div className="timeline edu bg-white rounded shadow-dark padding-30 overflow-hidden">
              {educationData.map((education) => (
                <Timeline key={education.id} education={education} />
              ))}
              <span className="line"></span>
            </div>
          </div>

          <div className="col-md-6">
            <div className="spacer d-md-none d-lg-none" data-height="30"></div>
            <div className="timeline exp bg-white rounded shadow-dark padding-30 overflow-hidden">
              {experienceData.map((experience) => (
                <Timeline key={experience.id} education={experience} />
              ))}
              <span className="line"></span>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default Experiences;
