import React from "react";

import Pagetitle from "../elements/Pagetitle";
import Portfolio from "../elements/Portfolio";

import InstaClone from "../../images/insta-clone.png";
import Instaclone1 from "../../images/instaclone1.png";
import Instaclone2 from "../../images/instaclone2.png";
import Instaclone3 from "../../images/instaclone3.jpg";
import Instaclone4 from "../../images/instaclone4.png";

import practo1 from "../../images/practo1.png";
import practo2 from "../../images/practo2.png";
import practo3 from "../../images/practo3.png";
import practo4 from "../../images/practo4.png";

import PractoClone from "../../images/practo.svg";

import ObjRecog from "../../images/obj-recog.png";
import Ecom from "../../images/ecom clone.png";
import Gtsrb from "../../images/gtsrb.svg";
import TrafficSign from "../../images/Traffic Sign.jpg";
import Ezlytics from "../../images/ezlytics.svg";

const allData = [
  {
    id: 1,
    title: "Healthnomics - Practo Clone",
    image: PractoClone,
    popupLink: [practo1, practo2, practo3, practo4],
    link: "https://github.com/nishanthbhat07/Healthnomics-Practo_Clone",
  },
  {
    id: 2,
    title: "Ecommerce Web App",
    image: Ecom,
    popupLink: [Ecom],
    link: "https://crwn-clothing-ecom-webapp.herokuapp.com/",
  },
  {
    id: 3,
    title: "Instagram Clone",
    image: InstaClone,
    popupLink: [InstaClone, Instaclone1, Instaclone2, Instaclone3, Instaclone4],
    link: "https://postergram.herokuapp.com/signin",
  },
  {
    id: 4,
    title: "Object Recognition",
    image: ObjRecog,
    link: "https://github.com/nishanthbhat07/ObjectRecognition-Using-CNN-",
  },
  {
    id: 5,
    title: "Traffic Sign Recognition",
    image: Gtsrb,
    popupLink: [TrafficSign],
    link: "https://github.com/nishanthbhat07/Deep-Learning-Projects/tree/main/GTSRB",
  },
  {
    id: 6,
    title: "Ezlytics- Data Analytics Tool",
    image: Ezlytics,
    link: "",
  },
];

function Works() {
  return (
    <section id="works">
      <div className="container">
        <Pagetitle title="Projects" />

        {/* Start Portfolio Items */}
        <div className="row portfolio-wrapper">
          {allData.map((item) => (
            <div className="col-md-4 col-sm-6 grid-item" key={item.id}>
              <Portfolio portfolio={item} />
            </div>
          ))}
        </div>
        {/* End Portfolio Items */}
      </div>
    </section>
  );
}

export default Works;
